<template>
  <div id="app" >
      <div>第{{n}}手</div>
      <div>
          <div class="row">
              <Cell @click="onClickCell(0,$event)" :n="n" />
              <Cell @click="onClickCell(1,$event)" :n="n"/>
              <Cell @click="onClickCell(2,$event)" :n="n"/>
          </div>
          <div class="row">
              <Cell @click="onClickCell(3,$event)" :n="n"/>
              <Cell @click="onClickCell(4,$event)" :n="n"/>
              <Cell @click="onClickCell(5,$event)" :n="n"/>
          </div>
          <div class="row">
              <Cell @click="onClickCell(6,$event)" :n="n"/>
              <Cell @click="onClickCell(7,$event)" :n="n"/>
              <Cell @click="onClickCell(8,$event)" :n="n"/>
          </div>
      </div>
      <div>结果：{{result === null ? '胜负未分' : `胜方为${result}`}}</div>

<!--      {{map}}-->
<!--      {{result}}-->

  </div>
</template>

<script>

import Cell from './Cell';
export default {

    components: {Cell},
    data() {
        return {
            n:0,
            map: [[null,null,null],
                 [null,null,null],
                 [null,null,null]],//为格子建立二维坐标
            result: null,

        };
    },
    methods: {
        onClickCell(i,text) {
            window.console.log('第'+i+'个cell被点击了,内容为：'+text);
            this.n+=1;
            this.map[Math.floor(i/3)][i%3] = text;//使格子坐标对应格子的内容
            this.tell();
        },
        tell() {
            const map = this.map;
            for(let i=0; i<2;i++) {
                window.console.log(map[i][0]);
                window.console.log(map[i][1]);
                window.console.log(map[i][2]);
                if(map[i][0] !== null && map[i][0] === map[i][1] && map[i][1] === map[i][2]) {
                    this.result = map[i][0];
                    //格子横向内容相等 且不为空时，有人胜利，this.text为胜利内容
                }
                if(map[0][i] !== null && map[0][i] === map[1][i] && map[1][i] === map[2][i]) {
                        this.result = map[0][i];
                        //格子竖向内容相等 且不为空时，有人胜利，this.text为胜利内容
                }
                if(map[0][0] !== null && map[0][0] === map[1][1] && map[1][1] === map[2][2]) {
                    this.result = map[0][0];
                    //格子斜向内容相等 且不为空时，有人胜利，this.text为胜利内容
                }
            }
        }

    },

}
</script>

<style>
    #app {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
.row {
  display:flex;
}
</style>
