<template>
    <div>
<!--        {{n}}-->
        <div class="cell" @click="onClickSelf()">
            <template v-if="a"></template>
            <template v-else >{{text}}</template>
        </div>
    </div>

</template>
<script>
export default {
    props:['n'],
     data() {
        return {
            a:'true',
            text: '',
        }
     },
    methods: {
         onClickSelf() {
             if(this.text!== '') {
                 return;
             }
             this.a = false;
             this.text = this.n % 2 === 0 ? 'x':`o`;
             this.$emit('click',this.text);
         }
    },

}
</script>

<style>
.cell {
  border:1px solid #080202;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
